html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* Global */
body {
  background-color: #141414;
  color: #ffffff;
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 130rem;
  width: 95%;
  margin: 0 auto;
  margin-inline: auto;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
  font-family: "Raleway", sans-serif;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
  color: #ffffff;
  margin: 0;
}

h3 {
  font-size: 3.2rem;
}

p {
  font-size: 1.6rem;
  line-height: 2;
  color: #ffffff;
  margin: 0;
}

a {
  text-decoration: none;
  color: #ffffff;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  /* para que no se muestre con un espacio abajo */
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.6rem 4rem;
}
.header__links {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__logo, .header__language {
  cursor: pointer;
}
.header__link {
  padding: 0.8rem 1.5rem 8px 1.5rem;
}
.header__link:hover a {
  color: #fe9a9d;
}

.main__grid {
  height: 100rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 1fr);
  gap: 2rem;
}

.card {
  background-color: #0d0d0e;
  border-radius: 1.5rem;
  padding: 3rem;
}
.card--about {
  grid-column: span 2;
  grid-row: span 2;
  text-align: left;
}
.card--about .card__content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.card--about .card__text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.card--image {
  grid-row: span 2;
  padding: 0;
  overflow: hidden;
}
.card--image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card--china {
  grid-column: 4/5;
  grid-row: 2/5;
}
.card--front {
  grid-column: 1;
  grid-row: span 2;
}
.card--sudio {
  grid-row: span 2;
  grid-column: span 2;
}
.card--tech {
  grid-column: span 2;
  grid-row: span 2;
}
.card--date {
  grid-row: 6/7;
  grid-column: 3/4;
}
.card--interests {
  grid-row: span 2;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text-gradient {
  background: linear-gradient(90deg, #FE9A9D 0%, #D798D9 50%, #B1D3F9 100%);
  font-weight: bold;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.text-mevn {
  background: linear-gradient(90deg, #42D392 0%, #647EFF 100%);
  font-weight: bold;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.projects {
  margin-top: 5rem;
}
.projects__heading {
  margin-bottom: 2rem;
}
.projects__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
}

.project {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #0d0d0e;
}
.project__content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
}
.project__title {
  font-size: 2rem;
  margin: 0;
}
.project__img {
  width: 25rem;
  background-position: center;
  background-size: cover;
  aspect-ratio: 16/9;
}
.project:nth-child(1) .project__img {
  background-image: url(../img/1.webp);
}
.project:nth-child(2) .project__img {
  background-image: url(../img/2.webp);
}
.project:nth-child(3) .project__img {
  background-image: url(../img/3.webp);
}
